import { Component, OnInit, OnDestroy } from '@angular/core';
import {PersonDetailsService} from '../../shared/person-details.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-person',
  templateUrl: './person.component.html',
  styleUrls: ['./person.component.css']
})
export class PersonComponent implements OnInit, OnDestroy {

  public userList: any;
  public singleUser: any;
  private subscription: Subscription;
  isloading : boolean;

  constructor(private person: PersonDetailsService) { }

  ngOnInit(): void {
    this.isloading = true;
    this.getPersonData();
  }

  getPersonData(){
    this.subscription = this.person.getUserData().subscribe( response => {
      this.userList = response[0];
      this.singleUser = response[1];
      this.isloading = false;
      console.log(this.singleUser);
    })
    
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}
