import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { PersonComponent } from './components/person/person.component';
import { AdditionalFormComponent } from './components/additional-form/additional-form.component';

//Auth Guard
import {AuthGuard} from './auth.guard';
import { ConfirmationGuard } from './confirmation.guard';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';

const routes: Routes = [
  {path:'', pathMatch:'full', redirectTo:'login'},
  {path:'login', component:LoginComponent},
  {path:'profile', component:PersonComponent, canActivate: [AuthGuard]},
  {path:'additionalform', component:AdditionalFormComponent, canActivate: [AuthGuard], canDeactivate:[ConfirmationGuard]},
  {path: '**', component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
