import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';

import {AuthService} from '../../shared/auth.service';
import {ConfirmationGuard, canComponenDeactive} from '../../confirmation.guard';

@Component({
  selector: 'app-additional-form',
  templateUrl: './additional-form.component.html',
  styleUrls: ['./additional-form.component.css']
})
export class AdditionalFormComponent implements OnInit, canComponenDeactive {
  userData : any;
  model: any = {};
  userName = '';
  password = '';
  submitted = false;

  constructor(private authService:AuthService, private router: Router) { }

  ngOnInit(): void {
    this.getUserData();
  }

  //Method to get user data from a local storage
  //usually used a Service to get loggedin user data 
  getUserData(){
    this.userData = JSON.parse(localStorage.getItem('userData'));
    console.log(this.userData);
    this.userName = this.userData.userName;
    this.password = this.userData.password;
  }

  //Form on Submit - Template driven form
  onSubmit() {
    this.submitted = true;
    this.router.navigate(['/profile']);
  }

  //this method is called when user want to leave AdditionalFormComponent without summiting the form
  confirm(){
    if(this.submitted){
      return true;
    }else{
      return confirm('Are you sure want to Leave?');
    }
    
  }

}
