import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class AuthService {

  private userData = {
    user_name: '',
    user_password: ''
  }

  userDetails: any;

  private selectedItem = new BehaviorSubject(this.userData);
  private selected = this.selectedItem.asObservable();

  constructor() { }

  public currentUser: any;

  public login(userInfo){
    if(userInfo.userName == 'santosh' && userInfo.password == 'santosh@123'){
      localStorage.setItem('ACCESS_TOKEN', "access_token");
      this.setUserData(userInfo);
      return true;
    }else{
      return false;
    }
  }

  public logout(){
    localStorage.removeItem('ACCESS_TOKEN');
  }
  
  public isLoggedIn(){
    this.currentUser = !!localStorage.getItem('ACCESS_TOKEN');
    return !!localStorage.getItem('ACCESS_TOKEN');
  }

  setUserData(userInfo:any){
    this.userDetails = userInfo;
    localStorage.setItem('userData', JSON.stringify(userInfo));
  }

  //public getUserData(): Observable<any[]>{
  public getUserData(){
    //return this.userDetails;
    //return JSON.parse(localStorage.getItem('userData'));
    let data = JSON.parse(localStorage.getItem('userData'))
    return data;
  }


}
